.ImageSection {
  display: flex;
  margin-top: 4rem;
  margin-bottom: 4rem;
  .img {
    width: 100%;
    height: 50vh;
    img {
      width: 100%;
      object-fit: cover;
      height: 100%;
    }
  }
  .about-info {
    margin-left: 2rem;
    h4 {
      font-size: 2rem;
    }
    .about-text {
      padding: 1rem 0;
      span {
        font-weight: 500;
        color: rgb(62, 245, 214);
      }
    }
  }
  @media screen and (max-width: 1400px) {
    flex-direction: column;
    .about-info {
      margin-left: 0;
      margin-top: 1rem;
    }
  }
}

.about-details {
  display: flex;
  flex-direction: row;
  p {
    padding: 0.3rem 0;
    font-size: 1.2rem;
  }
  .right-section {
    margin-left: 2.3rem;
  }
  .left-section {
    font-weight: 800;
    color: #8e1f67;
  }
}

.btn {
  margin-top: 2rem;
  margin-left: 1rem;
  padding: 1rem 2rem;
  background-color: #037fff;
  outline: none;
  border: none;
  font-family: inherit;
  font-size: inherit;
  color: white;
  text-transform: uppercase;
  cursor: pointer;
  letter-spacing: 2px;
  position: relative;
  a:visited {
    color: inherit;
    text-decoration: none;
  }
  &::after {
    position: absolute;
    content: "";
    width: 0;
    height: 0.3rem;
    left: 0;
    bottom: 0;
    background-color: #a4acc4;
    transition: all 0.4s ease-in-out;
  }
  &:hover::after {
    width: 100%;
  }
}
.awards-containers {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin-top: 1rem;
  @media screen and (max-width: 1400px) {
    grid-template-columns: repeat(1, 1fr);
    margin-bottom: 2rem;
  }
}
.experience-container {
  margin-bottom: 4rem;
}
.skills-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin-bottom: 1rem;
  @media screen and (max-width: 1100px) {
    grid-template-columns: repeat(1, 1fr);
    margin-bottom: 2rem;
  }
}
