.ProjectsPage {
}

.projects {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  row-gap: 2rem;
  column-gap: 1rem;
  .project {
    .image-data {
      position: relative;
      &::before {
        content: "";
        position: absolute;
        top: 12px;
        left: 15px;
        height: calc(100% - 30px);
        width: calc(100% - 30px);
        background-color: rgba(255, 255, 255, 0.925);
        transform-origin: center;
        transform: scale(0);
        transition: all 0.4s ease-in-out;
      }
      &:hover::before {
        transform: scale(1);
      }
      img {
        width: 100%;
        height: 30vh;
        object-fit: cover;
      }
      .hover-items {
        list-style: none;
        position: absolute;
        top: 70%;
        left: 50%;
        transform: translate(-50%, -50%);
        padding: 1rem 2rem;
        visibility: hidden;
        li {
          display: flex;
          a {
            padding: 1rem;
            text-decoration: none;
            font-family: inherit;
            border-radius: 10px;
            background-color: black;
            color: white;
            &:not(:last-child) {
              margin-right: 2rem;
            }
          }
        }
      }
      &:hover {
        .hover-items {
          transition: all 0.3s ease-in-out 0.2s;
          visibility: visible;
          top: 50%;
        }
      }
    }

    h2 {
      color: #8e1f67;
      transition: all 0.3s ease-in-out;
      &:hover {
        color: #037fff;
        cursor: pointer;
      }
    }
    ul {
      display: flex;
      li {
        margin-left: 0.2rem;
        list-style: none;
        color: black;
        .box {
          margin-top: 1rem;
          width: 100%;
          border-radius: 0.1rem;
          background: white;
          font-size: small;
          padding: 0.3rem;
        }
      }
    }
  }
}
