.App {
  .sidebar {
    width: 16%;
    height: 100vh;
    background-color: #191d2b;
    position: fixed;
  }
  .main-content {
    width: 86%;
    margin-left: 16%;
    background-color: #10121b;
    min-height: 100vh;
    background-image: url(../img/dots.svg);
    display: grid;
    position: relative;
    .content {
      margin: 5rem 15rem;
    }
  }
}
